.dashboard-container{
    display: flex;
    flex-direction: row;
}
.side-boxes{
    width: 100%;
}
/* .top-box-img{
width: 100%;
height: 235px;
} */
.topbox-header{
    text-align: center;
}
.top-box{
    padding: 15px;
    margin: 20px;
    background: url(../top-box.jpeg);
    background-size: cover;
    text-align: center;
    color: white;
}
.small-boxs{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.small-box{
    background-color: red;
    height: 180px;
    width: 370px;
    margin: 20px;

    text-align: center;
    font-size: 30px;
    color: white;
}
.process{
    margin: 20px;
    padding: 15px;
    background-color: rgb(22,157,81);
}
.process-box{
    display: flex;
    flex-wrap: wrap;
}

@media only screen and (max-width: 700px){
    .side-boxes{
        margin-left: 182px;
    }
}

@media only screen and (max-width: 600px) {
    .side-boxes{
        margin-left: 62px;
    }
    .small-box{
        background-color: red;
        width: 100%
    }
  }
